import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { from, Observable } from 'rxjs';
import { HttpService } from './http.service';
import { StorageService } from './storage.service';
import {AuthConstants} from './../configuration/auth-constants';
import { ILogin } from '../models/ILogin';
import { IRegister } from '../models/IRegister';
import { map, switchMap, take } from 'rxjs/operators';

@Injectable({
providedIn: 'root'
})
export class AuthService {
  constructor(
    private httpService: HttpService,
    private storageService: StorageService,
    private router: Router
  ) {}

  login(postData: ILogin): Observable<any> {
    return this.httpService.post('auth/login', postData).pipe(
      take(1),
      map(res => {
        let userStoregeObs: Observable<any> = from(this.storageService.store(AuthConstants.USER, res));
        return userStoregeObs;
      })
    )
  }

  signup(postData: IRegister): Observable<any> {
    return this.httpService.post('auth/register', postData);
  }

  async logout() {
    await this.storageService.removeStorageItem(AuthConstants.USER);
  }
}