import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { IWSMessagge } from '../models/IWSMessage';

@Injectable({
  providedIn: 'root'
})
export class WebSocketService {

  uri: string;
  ws: WebSocket = null;
  connection_state: boolean = false;
  timeout: any;
  msg: IWSMessagge;

  wsMsg = new Subject<IWSMessagge>();
  wsMsg$ = this.wsMsg.asObservable();

  constructor() { }

  createWebSocket(uri) {
    console.log("create ws");
    this.uri = uri;
    this.ws = new WebSocket(this.uri);
    this.ws.onerror = (error => {
      console.log('error en la conexion con Backend');
    })
        
    this.ws.onopen = (()=>{
      console.log('open WS');
      this.connection_state = true;
      if(this.timeout !== undefined){
        console.log("clear timeout");
        clearTimeout(this.timeout);
      }
    });
  
    this.ws.onmessage = ((msg)=>{
      this.msg = JSON.parse(msg.data);
      console.log(this.msg);
      this.wsMsg.next(this.msg);
      // this.handleMessageWS();
    });
  
    this.ws.onclose = (()=>{
      console.log('closed ev WS');
      this.connection_state = false;
      this.timeout = setTimeout(() => {
        console.log("set timeout ws");
        this.createWebSocket(this.uri);
      }, 5000);
    });
  }

  sendWebSocket(message: string) {
    console.log(message)
    this.ws.send(message);
  }
  
  closeWebSocket() {
      console.log('close WS');
      this.connection_state = false;
      if(this.ws !== null) {
        this.ws.close();
      }
  }
}
